.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card{
  box-shadow: 0 4px 8px 0 rgb(0, 0, 0);
  transition: o.3s;
  border-radius: 5px;
  height: 300px;
  width: 200px;
  margin: 5% 0% 5% 5%;
  
}

.align{
  display: block;
}

.col{
  float: left;
  padding: 3%;
}


.card:hover{
  box-shadow: 0 16px 32px 0 rgba(12, 12, 12, 0.548);
  
}

.bottom{
  padding: 2px 4px;

}



h1{
  background: rgb(185, 181, 181);
  color: rgb(119, 119, 119);
  text-align: center;
}

img{
  height: 60%;
  border-radius: 5px 5px 0 0;
  width: 100%;
  
}